}

.tab {
  display: none;
}

p.text-box {
  font-size: 16px;
  color: #333333;
  font-weight: 500;
  line-height: 2;
  letter-spacing: 0;
  font-family: "Noto Sans JP", sans-serif;
}

.text_white {
  color: #ffffff;
}

.align-left {
  text-align: left;
}

.align-center {
  text-align: center;
}

.align-rihgt {
  text-align: right;
}

.bgextend {
  position: relative;
  opacity: 0;
}

.bgextend:before {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: #3A863F;
}

.scroll-on .bgextend:before {
  animation: scroll-lead 1s ease-in-out forwards;
}

.scroll-on .bgextend {
  animation: bgextendAnimeBase 1s forwards;
}

.bgappearTrigger {
  opacity: 0;
}

.scroll-on .bgappearTrigger {
  animation: bgextendAnimeSecond 1s forwards;
  animation-delay: 0.6s;
}

.fadeInUp {
  opacity: 0;
}

.fadeInUp.scroll-on {
  animation: fadeUpAnime 1s forwards;
}

.button {
  height: 60px;
}

.button__color_orange {
  width: 240px;
  height: 60px;
  font-size: 16px;
  color: #ffffff;
  font-weight: 900;
  line-height: 1;
  letter-spacing: 0;
  text-transform: uppercase;
  background-color: #EC6A06;
  border-radius: 100vh;
  display: flex;
  align-items: center;
  filter: brightness(100%);
  transition: filter 0.3s ease;
}

.button__color_orange:hover {
  filter: brightness(110%);
  transition: filter 0.3s ease;
}

.button__color_orange a {
  width: 100%;
  text-align: center;
}

.button__color_white {
  width: 330px;
  height: 60px;
  font-size: 16px;
  color: #3A863F;
  font-weight: 900;
  line-height: 1;
  letter-spacing: 0;
  font-family: "Noto Sans JP", sans-serif;
  text-transform: uppercase;
  background-color: #ffffff;
  border-radius: 100vh;
  border: solid 2px #3A863F;
  display: flex;
  align-items: center;
  transition: all 0.3s ease;
}

.button__color_white:hover {
  color: #ffffff;
  background-color: #3A863F;
  transition: all 0.3s ease;
}

.button__color_white a {
  width: 100%;
  text-align: center;
}

.header {
  width: 100%;
  height: 101px;
  background-color: transparent;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1000;
  font-family: "Noto Serif JP", serif;
  border-bottom: solid 1px #ccc;
}

.header.standby {
  position: fixed;
  background-color: rgba(255, 255, 255, 0.8);
  transform: translateY(-100%);
}

.header.fixed {
  transform: translateY(0%);
}

.header__inner {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__logo {
  display: block;
  width: 349px;
  height: auto;
  overflow: hidden;
  aspect-ratio: 349/100;
}

.header__logo img {
  width: 100%;
  height: auto;
}

.header__right {
  display: flex;
  margin-left: auto;
  gap: 60px;
  height: 100%;
}

.header__button {
  width: 100px;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: relative;
}

.header__button-icon {
  width: 40px;
  height: 40px;
  aspect-ratio: 40/40;
  overflow: hidden;
}

.header__button-icon img {
  height: auto;
}

.header-mail {
  background-color: #3A863F;
}

.header-mail:hover {
  filter: brightness(120%);
}

.gloval-navi .menu {
  display: flex;
  align-items: center;
  gap: 60px;
  height: 100%;
}

.gloval-navi .menu-item.sp-active {
  display: none;
}

.gloval-navi .menu-item__icon {
  display: none;
}

.gloval-navi .nav-link {
  font-family: "Noto Sans JP", sans-serif;
  font-size: 18px;
  color: #ffffff;
  font-weight: 500;
  line-height: 100px;
  letter-spacing: 0;
  display: inline-block;
}

.gloval-navi .nav-link:hover {
  filter: brightness(120%);
}

.gloval-navi .nav-link.current {
  color: #3A863F;
  position: relative;
}

.gloval-navi .nav-link.current:after {
  content: "";
  display: block;
  width: 100%;
  height: 2px;
  background-color: #3A863F;
  position: absolute;
  left: 0;
  bottom: 0;
}

.standby .gloval-navi .nav-link {
  color: #575757;
}

.drawer-cta {
  display: none;
}

.drawer-cta__button {
  width: 254px;
  height: 70px;
  aspect-ratio: 254/70;
}

.drawer-cta__button img {
  height: auto;
}

.humberger {
  width: 8rem;
  height: 8rem;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 1100;
  cursor: pointer;
  background-color: #ffffff;
  display: none;
}

.humberger.fixed {
  position: fixed;
  top: 0;
  right: 0;
}

.humberger__line {
  width: 35px;
  height: 3px;
  background-color: #3A863F;
  margin-bottom: 7px;
  display: block;
}

.humberger__line:first-child {
  transition: transform 0.5s ease;
}

.humberger__line:nth-child(2) {
  opacity: 1;
  transition: opacity 0.3s ease;
}

.humberger__line:last-child {
  margin-bottom: 0;
  transition: transform 0.5s ease;
}

.menu-active .humberger__line:first-child {
  transition: transform 0.5s ease;
  transform: translateY(10px) rotate(45deg);
}

.menu-active .humberger__line:nth-child(2) {
  opacity: 0;
  transition: opacity 0.3s ease;
}

.menu-active .humberger__line:last-child {
  transition: transform 0.5s ease;
  transform: translateY(-10px) rotate(-45deg);
}

.humberger:before {
  content: "MENU";
  font-size: 10px;
  color: #3A863F;
  font-weight: 400;
  line-height: 1;
  letter-spacing: 0;
  display: block;
  text-align: center;
  line-height: 1;
  letter-spacing: 0;
  margin-bottom: 5px;
}

.main-visual {
  width: 100%;
  height: 980px;
  overflow: hidden;
  aspect-ratio: 1920/980;
  position: relative;
}

.main-visual img {
  height: 100%;
}

.main-visual__bg {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
}

.main-visual__inner {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
}

.main-visual__image {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
  opacity: 0;
  transform: scale(1.1);
  transition: all 3.5s cubic-bezier(0.455, 0.833, 0.515, 0.955), transform 3.5s cubic-bezier(0.455, 0.833, 0.515, 0.955);
}

.main-visual__image.is-visible {
  max-width: 100%;
  z-index: 1;
  opacity: 1;
  transform: scale(1);
  height: 100%;
}

.main-visual__container {
  height: 100%;
  display: flex;
  align-items: center;
}

.main-visual__textbox {
  position: relative;
  z-index: 1;
}

.main-visual__copy {
  font-size: 48px;
  color: #ffffff;
  font-weight: 900;
  line-height: 2;
  letter-spacing: 0.09em;
  text-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
}

.main-visual__link {
  font-size: 20px;
  color: #ffffff;
  font-weight: 500;
  line-height: 1;
  letter-spacing: 0;
  font-family: "Noto Sans JP", sans-serif;
  display: flex;
  align-items: center;
  margin-top: 50px;
}

.main-visual__link:after {
  content: "";
  display: inline-block;
  width: 100px;
  height: 2px;
  background-color: #ffffff;
  margin-left: 10px;
  animation: scroll-lead 3s cubic-bezier(1, 0, 0, 1) infinite;
}

.splash {
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 9999;
  background: #ffffff;
  text-align: center;
  color: #3A863F;
}

.splash__logo {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 200px;
}

.splash .fade-up {
  animation: zoomUpAnime 1.7s forwards;
  animation-delay: 0.2s;
  opacity: 0;
}

.splash.hide {
  display: none;
}

@media only screen and (max-width: 1600px) {
  .header__right {
    gap: 4rem;
  }

  .gloval-navi .menu {
    gap: 4rem;
  }

  .main-visual {
    height: 100vh;
  }
}

@media only screen and (max-width: 1300px) {
  html {
    font-size: 0.7692307692vw;
  }

  p.text-box {
    font-size: 1.6rem;
  }

  .text_white {
    color: #575757;
  }

  .button__color_orange {
    width: 24rem;
    height: 6rem;
    font-size: 1.6rem;
  }

  .button__color_white {
    width: 33rem;
    height: 6rem;
    font-size: 1.6rem;
  }

  .header {
    height: 10.1rem;
  }

  .header__logo {
    width: 34.9rem;
  }

  .header__button {
    width: 10rem;
    height: 100%;
  }

  .header__button-icon {
    width: 4rem;
    height: 4rem;
  }

  .gloval-navi .nav-link {
    font-size: 1.6rem;
    line-height: 10rem;
  }

  .humberger__line {
    width: 3.5rem;
    height: 0.3rem;
    margin-bottom: 0.7rem;
  }

  .menu-active .humberger__line:first-child {
    transform: translateY(1rem) rotate(45deg);
  }

  .menu-active .humberger__line:last-child {
    transform: translateY(-1rem) rotate(-45deg);
  }

  .main-visual__copy {
    font-size: 4.8rem;
  }

  .main-visual__link {
    font-size: 2rem;
    margin-top: 7.4rem;
  }
}

@media only screen and (max-width: 768px) {
  html {
    font-size: 1.3020833333vw;
  }

  .pc {
    display: none;
  }

  .tab {
    display: block;
  }

  .header {
    height: 8rem;
  }

  .header__inner {
    width: 100%;
    margin: 0;
  }

  .header__logo {
    width: 21.8rem;
  }

  .header__button {
    width: 8rem;
  }

  .gloval-navi {
    width: 100%;
    background-color: #ffffff;
    height: 100vh;
    position: fixed;
    top: 0;
    right: 0;
    z-index: 1;
    transform: translateX(100%);
    transition: transform 0.3s ease;
    opacity: 1;
    visibility: visible;
    padding: 8rem 0;
    display: flex;
    flex-direction: column;
    gap: 4rem;
  }

  .gloval-navi.open {
    transform: translateX(0%);
    transition: transform 0.3s ease;
  }

  .gloval-navi .menu {
    width: 91.2vw;
    padding: 0;
    margin: 0 auto;
    flex-direction: column;
    justify-content: center;
    gap: 0;
  }

  .gloval-navi .menu-item.sp-active {
    display: block;
  }

  .gloval-navi .menu-item {
    width: 100%;
    border-bottom: solid 1px #3A863F !important;
  }

  .gloval-navi .menu-item__icon {
    width: 1rem;
    height: 1.6rem;
    display: inline-block;
    background-image: url("../images/button-arrow.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }

  .gloval-navi .nav-link {
    line-height: 1;
    color: #333333;
    padding: 2rem 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .gloval-navi .nav-link.current:after {
    display: none;
  }

  .drawer-cta {
    display: flex;
    gap: 2rem;
    justify-content: center;
  }

  .humberger {
    display: flex;
  }

  .main-visual__copy {
    letter-spacing: 0;
    font-feature-settings: "palt";
  }
}

@media only screen and (max-width: 560px) {
  a[href^="tel:"] {
    pointer-events: auto;
  }

  .drawer-cta {
    flex-direction: column;
    align-items: center;
  }
}

@media only screen and (max-width: 428px) {
  html {
    font-size: 2.3364485981vw;
  }

  .header {
    height: 6rem;
  }

  .header__button {
    width: 6rem;
  }

  .humberger {
    width: 6rem;
    height: 6rem;
  }

  .main-visual__copy {
    font-size: 2.8rem;
  }
}